.background {
  background: transparent linear-gradient(220deg, #6CD3FF 0%, #09E93E 100%) 0% 0% no-repeat padding-box;
  opacity: 1;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font: 14px 'Open Sans', sans-serif;
}


.button {
  border-width: 2px;
  border-bottom-width: 6px;
  border-radius: 6px;
  opacity: 1;
  text-align: center;
  font: 24px 'Open Sans', sans-serif;
  letter-spacing: 0px;
  opacity: 1;
  padding: 8px 32px;
  border-style: solid;
}

.button:disabled {
  opacity: .2;
  filter: grayscale(1);
}

.button.green {
  color: #FFFFFF;
  background: #2FB833 0% 0% no-repeat padding-box;
  border-color: #39683b;
}

.button.red {
  color: #FFFFFF;
  background: #FF6565 0% 0% no-repeat padding-box;
  border-color: #A23B3B;
}

.button.gray {
  background: #2FB833 0% 0% no-repeat padding-box;
  border-color: #39683B;
  color: #000000;
}

.star {
  height: 64px;
  width: 64px;
}